@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
/* Google font import */

* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background: #f2f2f2;
  /*background: #eee;*/
  font-family: sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.react-quiz__header{background-color:#326273;display:flex;justify-content:center;color:white}.react-quiz__title{margin:40px 0}.react-quiz__quiz,.react-quiz__result{position:relative;max-width:800px;margin:50px 25px}@media (min-width: 769px){.react-quiz__quiz,.react-quiz__result{margin:0 auto}}.react-quiz__question{display:flex;justify-content:center;padding:40px 0;margin:0}.react-quiz__answer-options{display:flex;flex-direction:column;justify-content:space-evenly;list-style:none;margin:0;padding:0}@media (min-width: 769px){.react-quiz__answer-options{flex-direction:row}}.react-quiz__answer-option{background:#fff;box-shadow:3px 3px 5px 3px #d9d9d9}.react-quiz__answer-option:hover{background-color:#eefbfe}.react-quiz__radio-button,.react-quiz__radio-label{display:inline-block;vertical-align:middle;cursor:pointer}.react-quiz__radio-button{position:absolute;width:auto;opacity:0}.react-quiz__radio-label{position:relative;display:flex;align-items:center;justify-content:center;min-width:200px;min-height:80px;margin:0;font-size:18px;line-height:1.5}.react-quiz__result{padding:30px;background-color:#ffffff;font-size:18px;line-height:1.6}@media (min-width: 769px){.react-quiz__result{margin:50px auto}}.react-quiz__result-title{text-align:center;margin-top:0}.react-quiz__result-img{display:flex;justify-content:center;margin-top:30px}.react-quiz__img{max-width:350px}

